import "./style.css";

const canavs = document.querySelector("canvas");
const ctx = canavs.getContext("2d");

const gridElem = 40; // grille de 20 * 20  (800 / 40)
const sanke = [
  [9, 9],
  [9, 9],
  [9, 9],
];

const drawMap = () => {
  ctx.fillStyle = "black";
  ctx.fillRect(0, 0, 800, 800);
};

const drawSnake = () => {
  ctx.fillStyle = "green";
  for (let body of snake) {
    ctx.fillRect(body[0] * gridElem, body[1] * gridElem, gridElem, gridElem);
  }
};

drawMap();
drawSnake();
